@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap');

// colors
$primary: #3b74c9;
$secondary: lightgray;

html {
  font-family: 'Open Sans', sans-serif;
}

.container {
  display: flex;
  flex-direction: row;

  width: 100%;

  padding: 0;
}

.list-container {
  border: 1px solid lightgray;
  border-bottom: 0;
  border-top: 0;
  border-left: 0;

  height: 100vh;
  width: 30%;

  background-color: lighten(darkgray, 30%);
}

.chat-container {
  text-align: center;

  height: 100vh;
  width: 70%;
}

.choose-chat {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.list-header {
  display: flex;
  flex-direction: column;
  align-items: baseline;

  height: 90px;

  width: 100%;

  //border: 1px gray;
  border-bottom: 0.5px solid gray;
}

.search-input {
  margin: 0 10px;
  width: 90%;
  height: 25px;

  border: 0.5px solid gray;
  border-radius: 5px;

  font-size: 14px;

  text-align: start;

  padding: 5px;
}

input:focus {
  outline: none;
}

.single-chat-container {
  display: flex;
  flex-direction: row;

  width: 100%;

  height: 75px;
  border-bottom: 0.5px solid gray;

  margin-right: 5px;
}

.avatar-container {
  padding: 8px 8px;
}

.avatar{
  border: 1px solid lightgray;
  background-color: lightgray;
  border-radius: 50%;

  width: 55px;
  height: 55px;
}

.messenger-name {
  margin: 8px 0px;
}

.messenger-text {
  color: gray;
}

.message {
  display: flex;
  flex-direction: column;

  margin-right: 140px;
}

.info {
  display: flex;
  flex-direction: column;
}

.time {
  color: gray;

  padding: 8px 5px;
}

.counter {
  border: 1px solid $primary;
  background-color: $primary;
  color: white;

  border-radius: 50%;

  text-align: center;
  font-size: 14px;

  width: 20px;
  height: 20px;

  margin-left: 20px;
  margin-top: 3px;
}

.profile-container {
  display: flex;
  align-self: flex-end;
}

.profile {
  color: gray;
  margin: 10px 5px;
  text-decoration: none;
  font-size: 14px;
}